.for {
    padding-top: 100px;
    &__title {
        font-family: Montserrat;
        font-weight: bold;
        font-size: 40px;
        line-height: 60px;
        color: #EFF3FF;
        max-width: 480px;
        margin-top: 16px;

    }
    &__subtitle {
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: 1.1px;
        color: #FFA161;
    }
    &__descr {
        font-weight: normal;
        font-size: 16px;
        color: #EFF3FF;
        margin-top: 32px;
        opacity: 0.7;
        max-width: 380px;
    }
    &__wrapper {
        display:flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 48px;
    }
    &__item {
        width: 317px;
        height: 348px;
        background: rgba(61, 137, 225, 0.16);
        border-radius: 12px;
        padding-top: 35px;
        &-icon {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 72px;
            height: 72px;
            border-radius: 100%;
            background: #11437C;
            margin: 0 auto;
        }
        &-title {
            font-weight: bold;
            font-size: 16px;
            line-height: 24px;
            color: #EFF3FF;
            text-align: center;
            margin-top: 24px;
        }
        &-descr {
            font-weight: normal;
            font-size: 14px;
            line-height: 24px;
            text-align: center;
            color: #EFF3FF;
            opacity: 0.7;
            margin-top: 8px;
            max-width: 270px;
            margin: 0 auto;
        }
        &-btn {
            display: flex;
            width: 137px;
            height: 48px;
            background: rgba(255, 160, 97, 1);
            border-radius: 6px;
            margin: 0 auto;
            font-weight: 600;
            font-size: 16px;
            line-height: 24px;
            color: #933A14;
            justify-content: center;
            align-items: center;
            transition: all .5s;
            cursor: pointer;
            border: 1px solid rgba(239, 243, 255, 0);
            margin-top: 24px;
            &:hover {
                background: rgba(255, 160, 97, 0);
                border: 1px solid rgba(239, 243, 255, 1);
                color: #EFF3FF;
            }
        }
    }
}