.main {
    min-height: 575px;
    padding-top: 30px;
    &__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__arr {
        position: absolute;
        bottom: -46%;
        right: -17%;
    }
    &__info {
        position: relative;
        &-subtitle {
            font-weight: normal;
            font-size: 16px;
            line-height: 24px;
            color: #FEA05D;
        }
        &-title {
            font-family: Montserrat;;
            font-weight: bold;
            font-size: 60px;
            line-height: 80px;
            color: #EFF3FF;
            margin-top: 20px;
        }
        &-descr {
            font-weight: normal;
            font-size: 16px;
            color: #EFF3FF;
            margin-top: 25px;
        }
        &-buttons {
            margin-top: 48px;
            display: flex;
            align-items: center;
        }
        &-get {
            width: 139px;
            height: 48px;
            background: #FFA161;
            border-radius: 6px;
            font-weight: 600;
            font-size: 16px;
            color: #933A14;
            margin-right: 24px;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        &-watch {
            width: 150px;
            height: 48px;
            border: 1px solid #EFF3FF;
            border-radius: 6px;
            font-weight: 600;
            font-size: 16px;
            color: #EFF3FF;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
}