.header {
    &__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__logo {
        font-weight: bold;
        font-size: 24px;
        color: #FEA05D;
    }
    
    &__nav {
        &-list {
            list-style-type: none;
            margin: 0;
            padding: 0;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        &-item {
            margin-right: 40px;
            &:last-child {
                margin-right: 0;
            }
        }
        &-link {
            font-weight: 600;
            font-size: 16px;
            letter-spacing: 0.011em;
            color: #EFF3FF;
            text-decoration: none;
            &:hover {
                text-decoration: none;
                color: #EFF3FF;
            }
        }
    }

    &__reg {
        display: flex;
        justify-content: space-between;
        justify-content: center;
    }

    &__btn {
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: 600;
        font-size: 16px;
        line-height: 24px;
        color: #EFF3FF;
        text-decoration: none;
        &:hover {
            text-decoration: none;
        }
    }

    &__btnup {
        width: 111px;
        height: 48px;
        background: #FFA161;
        border-radius: 6px;
        color: #933A14;
        margin-left: 40px;
    }
}