.promo {
    padding-top: 125px;
    &__subtitle {
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;
        color: #FFA161;
        text-align: center;
    }
    &__title {
        font-family: Montserrat;
        font-style: normal;
        font-weight: bold;
        font-size: 40px;
        text-align: center;
        color: #EFF3FF;
        max-width: 420px;
        margin: 0 auto;
        margin-top: 16px;
    }
    &__descr {
        font-weight: normal;
        font-size: 14px;
        line-height: 18px;
        color: #EFF3FF;
        opacity: 0.6;
        margin-top: 24px;
        text-align: center;
    }
    &__wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 200px;
        font-weight: normal;
        font-size: 14px;
        line-height: 18px;
        color: #EFF3FF;
        margin: 0 auto;
        margin-top: 24px;
    }
    &__switch {
        position: relative;
        width: 54px;
        height: 24px;
        background: rgba(239, 243, 255, 0.3);
        border-radius: 16px;
        cursor: pointer;
    }
    &__round {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 4%;
        width: 20px;
        height: 20px;
        border-radius: 100%;
        background: #EFF3FF;
        transition: all .5s;
        cursor: pointer;
        &.active {
            left: 57%;
            transition: all .5s;
        }
    }
    &__text {
        position: relative;
        max-width: 155px;
        margin: 0 auto;
        margin-top: 24px;
        font-weight: 600;
        font-size: 14px;
        color: #FFA161;
    }
    &__arr {
        position: absolute;
        top: -85%;
        right: 0;
    }
    &__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__item {
        width: 282px;
        height: 604px;
        border: 1px solid #EFF3FF;
        border-radius: 10px;
    }
}