.footer {
    min-height: 370px;
    &__wrapper {
        display: flex;
        justify-content: space-between;
    }
    &__info {
        max-width: 360px;
        &-logo {
            font-weight: bold;
            font-size: 24px;
            color: #FEA05D;
        }
        &-descr {
            font-weight: normal;
            font-size: 16px;
            line-height: 32px;
            color: #EFF3FF;
            margin-top: 15px;
        }
    }
    &__links {
        display: flex;
        width: 60%;
        justify-content: space-around;

        &-title {
            font-weight: bold;
            font-size: 16px;
            color: #EFF3FF;
        }
        &-list {
            margin: 0;
            padding: 0;
            list-style-type: none;
        }
        &-item {
            margin-top: 24px;
            a {
                font-weight: normal;
                font-size: 14px;
                color: #EFF3FF;
                text-decoration: none;
                &:hover {
                    text-decoration: none;
                }
            }
        }
    }
    &__descr {
        &-title {
            font-weight: bold;
            font-size: 16px;
            color: #EFF3FF;
        }
        &-text {
            font-weight: normal;
            font-size: 14px;
            color: #EFF3FF;
            margin-top: 20px;
        }
    }
}